 import * as THREE from '../../node_modules/three/build/three.module';
 import {SVGLoader} from "../../node_modules/three/examples/jsm/loaders/SVGLoader"
 import {SVGRenderer,SVGObject} from "../../node_modules/three/examples/jsm/renderers/SVGRenderer"




let timeout=null;
let camera;

let scene, renderer, svgLoader;
let character,group;
let tan, ele;
let pointNum=0,originVer=[],oDestiVer=[],destiVer=[],oVer=[];

let url;
export function init(url_){
    url=url_;
    let container = document.getElementById('ani');
    svgLoader = new SVGLoader();
    camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 1, 10000);
    camera.position.z = 300;
    camera.position.y= 0;
    scene = new THREE.Scene();
    group=new THREE.Group();scene.add(group);
    character=new THREE.Group();
    character.name="character";

    group.add(character);
    
 
    camera.lookAt(scene.position);


    for(let i=0;i<url.length;i++){
        svgLoader.load(url[i],
        svg_loading_done_callback1,
        function (xhr) { console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded') },
        function () {console.log("Error loading SVG!"); });
    }

    group.rotation.x=Math.PI;
    //     group.translateY(-75);
    group.translateZ(100);
    character.translateY(100);


    renderer = new SVGRenderer();
    renderer.setClearColor(0x000000);
    renderer.setSize(container.clientWidth, container.clientHeight);
    container.appendChild(renderer.domElement);

}


function svg_loading_done_callback1(doc){
    console.log(doc);
    init1(doc);      
    if(character.children.length==url.length){
        if(character.children[0].children.length==11){
            tan=0;ele=1;
        }else{
            tan=1;ele=0;
        }
        character.children[tan].name="tan";
        character.children[ele].name="elephant";
        groupRotate()

        //拷贝原数组；
        let obj={x:0,y:125,z:0};
        for(let i=0;i<pointNum;i++){
            oDestiVer.push(obj);
            oVer.push(obj);
        }             
        character.children[ele].visible=false;
        render();
    }
}

function init1(data) {//人物初始化
    console.log(`data`)
    console.log(data);
    let groupT = new THREE.Group();
    groupT.name=data.paths.length==11?"tan":"elephant";
    addGeoObject1(groupT,data);
    let box = new THREE.Box3();
    box.setFromObject(groupT);
    for(let j=0;j<groupT.children.length;j++){
        groupT.children[j].position.x -= box.getCenter().x;
        groupT.children[j].position.y -= box.getCenter().y;
    }
    
    character.add(groupT );
}

function addGeoObject1(group, svgObject) {
    let i, j, localLen, len1;
    let simpleShapes, simpleShape;
    let thePaths = svgObject.paths;
    localLen = thePaths.length;
    console.log(`svg`)
    console.log(svgObject)
    for (i = 0; i < localLen; ++i) {

        //必须都是true，否则孔洞或外轮廓会丢失
        simpleShapes = thePaths[i].toShapes(true, true);
    //   simpleShapes = thePaths[i];                  

        len1 = simpleShapes.length;
        for (j = 0; j < len1; ++j) {
            simpleShape = simpleShapes[j];
            simpleShape.autoClose = true;

            //获取点序列的采样方法来自虚基类CurvePath，默认参数12段
            //P.S样条曲线也是这样采样连线显示的（官方文档搜splinecurve）
            let points = simpleShape.createPointsGeometry();
            let cPoints=points.clone();
            let pointsMaterial = new THREE.PointsMaterial({
                color:0xffffff,
                size:0.7,
                transparent:false,//使材质透明
                blending:THREE.AdditiveBlending,
                depthTest:true,//深度测试開啟，不消去场景的不可见面
            //  map:createLightMateria()//刚刚创建的粒子贴图就在这里
            });

            let originParticleField = new THREE.Points(points,pointsMaterial);
            pointNum+=points.vertices.length;
            originVer.push(...points.vertices);
            destiVer.push(...cPoints.vertices);
            group.add(originParticleField);
        }   
    }
}


function groupRotate(){
 //   console.log(character.children[tan].children.length);
    character.children[tan].children[9].position.z+=-10;
    character.children[tan].children[9].quaternion.setFromAxisAngle(new THREE.Vector3(0,1,0),Math.PI/8);
    
    character.children[tan].children[10].position.z+=10;
    character.children[tan].children[10].quaternion.setFromAxisAngle(new THREE.Vector3(0.5,1,0),Math.PI/8);

    for(let i=8;i<=13;i++){
        character.children[ele].children[i].position.z+=-9;
        character.children[ele].children[i].quaternion.setFromAxisAngle(new THREE.Vector3(1,0,0),-Math.PI/8);
    }
}

function render() {
    throttle(renderer.render(scene, camera),100);
}

function throttle(fn,delay){
    return function(){
        if(!timeout){
            timeout=setTimeout(function(){
                fn();
                clearTimeout(timeout);
                timeout=null;
            },delay)
        }
    }
}


 
